import React from 'react';
import s from './Post.module.css';

type PostPropsType = {
    id?: string
    message: string
    likesCount: number
}

export const Post = React.memo((props: PostPropsType) => {
    return <div className={s.item}>
        <img alt={'user avatar'}
            src='data:image/jpeg;base64,'/>
        {props.message}
        <div>
            <span>Likes {props.likesCount}</span>
        </div>
    </div>
})
